import React, { Component } from 'react';
import { Calendar, momentLocalizer } from 'react-big-calendar';
import 'react-big-calendar/lib/css/react-big-calendar.css';
import { render } from 'react-dom';
import dateFormat, { masks } from "dateformat";
import moment from 'moment';

const localizer = momentLocalizer(moment);
const apiUrl = 'http://localhost:8081/api/v1/appt/all';


/**
 * A calendar event.
 */
class Event {
    constructor(id, title, description, start, end) {
        this.id = id;
        this.title = title;
        this.description = description;
        this.start = start;
        this.end = end;
    }
  }

/**
 * Creates a new react-style calendar as the main interface to manage appointments.
 */
class AppointmentCalendar extends React.Component {


    /**
     * Initialize the calendar object.
     * @param {*} props 
     */
    constructor(props) {
        super(props);

        const now = new Date();
        
        const events = [
            {
                id: 0,
                title: 'All Day Event very long title',
                allDay: true,
                start: new Date(2022, 7, 10),
                end: new Date(2022, 7, 11),
            }
        ];

        // Properties of the calendar.
        this.state = {
            appointments: [],
            selectedAppointment: null,
            selectedAppointmentDate: null,
            selectedAppointmentTime: null,
            selectedAppointmentDuration: null,
            selectedAppointmentDescription: null,
            selectedAppointmentLocation: null,
            selectedAppointmentContact: null,
            selectedAppointmentNotes: null,
            selectedAppointmentId: null,
            selectedAppointmentType: null,
            selectedAppointmentStatus: null,
            selectedAppointmentColor: null,
        }
    }

    /**
     * Fetches the appointments from the API.
     */
    async fetchData(url) {
        const fetchOptions = {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json',
            }
        }

        const response = await fetch(url, fetchOptions);
        console.log(response);
        const code = response.status;
        if (code === 200) {
            console.log("Success");
            const data = await response.json();
            console.log(data);
            return data;
        }
        else {
            throw new Error(`Error fetching data: ${code}`);
            return null;
        }
    }
    
    /**
     * Converts a java.time.Instant string to a Date object.
     * @param {*} instant 
     * @returns 
     */
    convertInstantToDate(instant) {
        // Convert the instant to a Date object.
        if(instant == null) {
            throw new Error(`Instant is null`);
        }

        // Try to parse the instant.
        const date = new Date(instant);
        if (date.getTime() === 0) {
            // The instant was not parseable.
            console.warn("Could not parse instant: " + instant);
            return null;
        }
        else {
            // The instant was parseable.
            return date;
        }
    }

    /**
     * Load all the appointments from the database.
     * @returns {*} The appointments from the database.
     */
    loadAppointments() {    
        // Load the appointments from the database.
        this.fetchData(apiUrl).then(data => {
            // For each appointment, create a new event object.            
            const events = [];
            for (let i = 0; i < data.length; i++) {
                const appt = data[i];
                const start = this.convertInstantToDate(appt.startTime);
                const end = this.convertInstantToDate(appt.endTime);
                const event = new Event(appt.id, appt.name, appt.description, start, end);
                events.push(event);
            }
            // Update the state with the appointments.
            this.setState({
                appointments: events,
            });
            this.render();
        }
        ).catch(error => {
            console.log(error);
        }
        );
        console.log(this.state.events);
    }

    
    /**
     * Called when the component is mounted.
     * @returns {*}
     */
     componentDidMount() {
        this.loadAppointments();
    }

    formats = {
        eventTimeRangeFormat: () => { 
          return "";
        },
    };
    

    /**
     * PUT request to update an appointment.
     * @param {*} event 
     */
    async updateAppointment(event) {
        const fetchOptions = {
            method: 'PUT',
            headers: {
                'Content-Type': 'application/json',
            },
            mode: 'cors',
            body: JSON.stringify(event)
        }

        const response = await fetch("http://localhost:8081/api/v1/appt/appts" + "/" + event.id, fetchOptions);
        console.log(response);
        const code = response.status;
        if (code === 204) {
            console.log("Success");

        }
        else {
            throw new Error(`Error updating appointment: ${code}`);
        }
        // Reload the appointments.
        this.loadAppointments();
    }
    

    /**
     * Alert the full details of the selected event.
     * @param {*} event The calendar event.
     */
    alertAppointmentDetails(event) {
        // Prompt the user for a new name
        const name = prompt("Enter a new name for the event or type 'cancel' to cancel");
        if (name == null || name === "") {
            // The user cancelled.
            alert(JSON.stringify(event));
            return;
        }

        // Check if the name is 'delete'.
        if (name.toLowerCase() === 'cancel') {
            // Delete the event.
            this.deleteAppointment(event);
            return;
        } else {
            // Update the event.
            event.title = name;
            this.updateAppointment(event);
        }




        alert(JSON.stringify(event)); // TODO: Implement this.
        this.setState({
            selectedAppointment: event,
            selectedAppointmentDate: event.start,
            selectedAppointmentTime: event.start,
            selectedAppointmentDuration: event.end,
            selectedAppointmentDescription: event.description,
            selectedAppointmentLocation: event.location,
            selectedAppointmentContact: event.contact,
            selectedAppointmentNotes: event.notes,
            selectedAppointmentId: event.id,
            selectedAppointmentType: event.type,
            selectedAppointmentStatus: event.status,
            selectedAppointmentColor: event.color,
        });
    }

    /**
     * DELETE the selected appointment.
     * @param {*} event The calendar event.
     */
    async deleteAppointment(event) {
        // Delete the appointment.
        const fetchOptions = {
            method: 'DELETE',
            headers: {
                'Content-Type': 'application/json',
            }
        }

        const response = await fetch("http://localhost:8081/api/v1/appt/appts" + "/" + event.id, fetchOptions);
        console.log(response);
        const code = response.status;
        if (code === 204) {
            console.log("Success");

        }
        else {
            throw new Error(`Error deleting appointment: ${code}`);
        }
        // Reload the appointments.
        this.loadAppointments();
    }

    /**
     * POST a new appointment at the given slot and post it to the database.
     * @param {*} event drag event.
     */
    async createAppointment(event) {
        // Prompt the user for a name.
        const name = prompt("Name");
        if (name == null) {
            alert("Cancelled");
            return;
        }

        // Create a new appointment.
        // var dateFormat = require('dateformat');
        const start = dateFormat(event.start, "yyyy-mm-dd HH:MM:ss");
        const end = dateFormat(event.end, "yyyy-mm-dd HH:MM:ss");
        const appt = {
            name: name,
            startTime: start,
            endTime: end,
            description: "",            
        }
        // Post the appointment to the database.
        const fetchOptions = {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json',
            },
            mode: 'cors',
            body: JSON.stringify(appt)
        }
        const response = await fetch('http://localhost:8081/api/v1/appt/appts', fetchOptions);
        const code = response.status;
        if (code === 201) {
            console.log("Success");
            const data = await response.json();
            console.log(data);
            this.loadAppointments();
        }
        else {
            throw new Error(`Error creating appointment: ${code}`);
            return null;
        }
    }

    /**
     * Renders the calendar view to an object
     * @returns {*} The calendar view 
     */
    render() {
        return (
          <div>
            <div style={{ height: '500pt'}}>
              <Calendar
                events={this.state.appointments}
                startAccessor="start"
                endAccessor="end"
                view={'week'}
                defaultDate={moment().toDate()}
                localizer={localizer}
                selectable={true}
                handleDragStart={event => {
                    this.createAppointment(event);
                }
                }
                onSelectSlot={event => { this.createAppointment(event); }}
                onSelectEvent={slotInfo => { this.alertAppointmentDetails(slotInfo); }}
              />
            </div>
          </div>
        );
      }
}

// console.log("Rendering AppointmentCalendar");
// render(<AppointmentCalendar/>, document.getElementById('root'));

export default AppointmentCalendar;